.navbar{
    height: 60px;
    border-bottom: 0.5px solid #ffe4e4;
    display: flex;
    align-items: center;
    font-size: 15px;
    color: rgb(97, 97, 97);

    .wrapper{
        width: 100%;
        padding: 25px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        .search{
            display: flex;
            align-items: center;
            border: 0.5px solid #ffe4e4;
            padding: 3px;
            input{
                border:none;
                outline :none;
                background-color: transparent;
            }
        }
        .items{
            display:flex;
            align-items: center;

            .item{
                display: flex;
                align-items: center;
                margin-right: 20px;

                .icon{
                    font-size: 30px;
                }
            }


        }
    }
}