.sidebar{    
    flex: 1;
    border-right:0.5px solid #ffe4e4 ;
    min-height: 100vh;
    background-color: rgb(255, 255, 255);

    a {
        text-decoration: none;
    
        .icon {
          vertical-align: middle;
        }
    
        span {
          vertical-align: middle;
        }
      }

    .top{
        
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;

        .logo{
            width: 162.3;
            height: 51.72px;
        }

    
    }

    hr{
        height: 0;
        border: 0.5px solid #ffe4e4 ;
    }

    .center{
        padding-left: 10px;
        ul{
            list-style: none;
            margin: 0;
            padding: 0;
            .title{
                font-size: 15px;
                font-weight: bold;
                color: rgb(97, 97, 97);
                margin-top: 15px;
                margin-bottom: 5px;
            }


        }

        li{
            display: flex;
            align-items: center;
            padding: 8px;
            cursor: pointer;
            

            &:hover{
                background-color: rgb(255, 235, 235);
            }
            .icon{
                font-size: 18px;
                color: rgb(226, 39, 39);
                
            }
            span{
                font-size: 14px;
                font-weight: 600;
                color: grey;
                margin-left: 10px;
            }


        }
        
    }



}