.widget{
    display: flex;
    justify-content: space-between;    
    flex: 0 1 135px;
    padding: 15px;
    box-shadow: 1px 1px 5px 2px rgba(191,0,0,0.36);
    -webkit-box-shadow: 1px 1px 5px 2px rgba(191,0,0,0.36);
    -moz-box-shadow: 1px 1px 5px 2px rgba(191,0,0,0.36);
    border-radius: 10px;

    .left,.right{
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .title{
            font-weight: bold;
            font-size: 15px;
            color:  rgb(97, 97, 97);
        }

        .counter{
            font-size: 25px;
            font-weight: 300;
        }

        .link{
            width: max-content;
            font-size: 12px;
            border-bottom: 1px solid gray;
            
        }

        .icon{
            font-size: 30px;
            padding: 0px;
            color: rgb(226, 39, 39);
            border-radius: 10px;
            margin-top: -15px;
            margin-right: -15px;
        } 
    }


}