.not-found {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;

    h1 {
        font-size: 48px;
        margin-bottom: 200px;
        color: #ff4081; // Pink color for heading
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3); // Shadow effect for heading
        animation: crazy 2s infinite; // Apply the "crazy" effect
    }

    .button {
        width: 200px; // Adjust the width
        height: 100px; // Adjust the height
        padding: 20px; // Adjust the padding
        background-color: #ff4081; // Change button color
        color: white;
        border: none;
        border-radius: 24px;
        font-size: 50px;
        font-weight: bold; // Add font weight
        text-transform: uppercase; // Add text transformation
        cursor: pointer;
        transition: background-color 0.3s ease;

        &:hover {
            background-color: #e53935;
        }
    }

    @keyframes crazy {
        0% {
            transform: rotate(0deg);
        }
        50% {
            transform: rotate(360deg);
        }
        100% {
            transform: rotate(0deg);
        }
    }
}
