.login {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #ffffff;
    flex-direction: column; // BEGIN: Add this line to stack the elements vertically

    .logo {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 10px;
        flex-direction: column;
        max-width: calc(33.33% - 20px); // Adjusted the max-width to be one-third of the horizontal width
        margin: 0 auto; // Add this line to center the logo horizontally
        margin-bottom: 20px; // Add this line to create margin between the logo and input
    }

    form {
        display: flex;
        flex-direction: column;
        width: 300px;
        padding: 20px;
        border-radius: 5px;
        background-color: #ffffff;
        box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
        justify-content: center;
        align-items: center;

        input {
            margin-bottom: 10px;
            padding: 10px;
            border-radius: 5px;
            border: 1px solid #ddd;
            text-align: center;

            &.error {
                border-color: red;
            }
        }

        button {
            padding: 10px;
            border-radius: 5px;
            border: none;
            background-color: #007bff;
            color: #ffffff;
            cursor: pointer;

            &:hover {
                background-color: #0056b3;
            }
        }
    }
}