.featured{    
    flex: 1; // Adjusted flex value to make it narrower
    box-shadow: 1px 1px 5px 2px rgba(191,0,0,0.36);
    -webkit-box-shadow: 1px 1px 5px 2px rgba(191,0,0,0.36);
    -moz-box-shadow: 1px 1px 5px 2px rgba(191,0,0,0.36);
    border-radius: 10px;
    padding: 10px;

    .left, .right{
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        
        .subtitle{
            font-weight: bold;
            font-size: 15px;
            color:  rgb(97, 97, 97);

        }

        .title{
            justify-content: baseline;
            font-weight: bold;            
            font-size: 25px;
            color:  rgb(97, 97, 97);
            margin-bottom: 15px;
            margin-top: 0px;
            stroke: #d82424;
            padding-left: 15px;

        }

        .names{
            display: flex;            
            flex-direction: column;
            font-size: 20px;
            font-weight: 300;
            
           
        }        

        
    }
}
